.footer {
  display: flex;
  flex-direction: column;
  background-color: #ff7779;
  color: #ffffff;
  padding: 1% 10%;
}
.footContainer {
  display: flex;
  justify-content: space-between;
}
.footBar {
  justify-content: start;
  text-align: left;
}
.footer-link {
  color: #ffffff;
  text-decoration: none;
}

.footer-link:hover {
  color: #ffffff;
  text-decoration: none;
}

.footIcons img {
  width: 26px;
  margin: 10px;
}
.footerLogo img {
  width: 70%;
  margin-right: 0%;
}
.footContainer2 .footer-btn {
  cursor: pointer;
  outline: none;
  border: none;
  background-color: rgb(248, 45, 147);
  /* width: 140px; */
  height: 40px;
  border-radius: 30px;
  font-size: 1rem;
  font-weight: 600;
  /* color: var(--text); */
  background-size: 100% 100%;
  /* box-shadow: 0 0 0 7px var(--light) inset; */
  margin: 15px;
  color: #ffffff;
}

.footer-btn:hover {
  border-color: rgb(248, 45, 147);
  background-color: #ffffff;
  color: rgb(248, 45, 147);
}

.logIn {
  display: flex;
  align-items: start;
}

.searchInput input {
  width: 250px;
  height: 30px;
  padding-left: 15px;
  border-radius: 30px;
  border: 2px solid #fff;
  background: #ffffff;
  outline: none;
  transition: 0.3s linear;
}
.footContainer2 {
  display: flex;
  justify-content: space-between;
}
@media all and (max-width: 400px) {
  .footer {
    display: flex;
    flex-direction: column;
  }
  .searchInput {
    display: flex;
  }
  .footerLogo img {
    width: 150px;
    margin-right: 0%;
  }
  .footContainer {
    display: flex;
    flex-direction: column-reverse;
  }
  .footContainer2 {
    display: flex;
    flex-direction: column;
  }
  .searchInput {
    width: 100%;
  }
  .logIn {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 100;
  width: 100%;
}

.header__icon {
  object-fit: contain;
  height: 100px;
  margin-left: 40px;
}

.header__center {
  display: flex;
  flex: 1 1;
  align-items: center;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  padding: 10px;
  height: 30px;
  border: 1px solid lightgray;
  border-radius: 999px;
}

.header__center > input {
  border: none;
  padding: 10px;
  outline-width: 0;
  /* width: 250px; */
}

.header__right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 15vw;
  margin-right: 80px;
}

.banner {
  background: url("https://media.cntraveler.com/photos/5db1d0dd11c1e500092e7133/master/pass/airbnb-ski-aspen-28328347.jpg")
    center center;
  height: 60vh;
  position: relative;
}

.banner__info {
  background-color: black;
  color: white;
  padding-top: 20vh;
  padding-left: 30px;
  padding-right: 50px;
  padding-bottom: 40px;
  width: 350px;
}

.banner__search {
  display: flex;
  flex-direction: column;
}

.banner__searchButton {
  background-color: white !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  color: #ff7779 !important;
}

.banner__info > button {
  background-color: #ff7779;
  color: white;
  text-transform: inherit;
  /* margin-top: 20px; */
  font-weight: 600;
  width: 150px;
}

.banner__info > h1 {
  margin-bottom: 10px;
}

.banner__info > h5 {
  margin-bottom: 10px;
}

.banner__info > button:hover {
  background-color: white;
  color: #ff7779;
}

.search {
  position: absolute;
  top: 35px;
  left: 25%;
  width: 100vw;
}

.search > h2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 559px;
  padding: 10px;
  background-color: white;
  position: absolute;
  left: 0;
  top: 340px;
}

.search > input {
  width: 539px;
  padding: 20px;
  position: absolute;
  left: 0;
  height: 20px;
  top: 380px;
  border: none;
}

.search > input :focus {
  outline-width: 0;
}

.search > button {
  position: absolute;
  left: 0;
  top: 430px;
  text-transform: inherit !important;
  background-color: #ff7779;
  color: white;
  width: 579px;
}

.search > button :hover {
  background-color: white;
  color: #ff7779;
}

.card {
  margin: 10px;
  border: 0px solid black;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 6px 18px -9px rgba(0, 0, 0, 0.75);
  transition: transform 100ms ease-in;
  cursor: pointer;
}

.card :hover {
  transform: scale(1.04);
}

.card > img {
  object-fit: fill;
  min-width: 300px;
  min-height: 200px;
  width: 100%;
}

.card__info {
  margin-top: -9px;
  border-radius: 10px;
  padding: 20px;
  padding-top: 20px;
  border: 1;
}

.card__info > h2 {
  font-size: 18px;
  font-weight: 600;
}

.card__info > h4 {
  font-size: 14px;
  font-weight: 300;
  margin-top: 8px;
  margin-bottom: 8px;
}

.home__section {
  display: flex;
  padding: 30px;
}

.category {
  margin-left: 40px;
}

