.block-product-details{
    display: flex;
    margin-top: 50px;
    padding-bottom: 50px;
}

.details-description{
    text-align: start;
    padding-left: 40px;
}

.btn-add-to-cart{
    height: 40px;
    cursor: pointer;
    margin-right: 10px;
    padding: 6px 10px;
    border-radius: 30px;
    font-size: 1rem;
    font-weight: 600;
    background-color: #573ba3;
    color: #fff;
}
.btn-add-to-cart:hover{ 
    transform: scale(1.03);
    transition: 0,5s;
}

.btn-add-to-wishlist{
    height: 40px;
    border: 1px solid#573ba3;
    background-color: inherit;
    cursor: pointer;
    background-color:  #fff;
    border-radius: 30px;
    font-size: 1rem;
    font-weight: 600;
    margin: 30px;
    color:  #573ba3;
}
.btn-add-to-wishlist:hover{ 
    transform: scale(1.03);
    transition: 0,5s;
}

.detail-block-description{
    padding-top: 40px;
}
.save-comment-btn{
    /* width: 300px; */
    height: 40px;
    border: 1px solid#573ba3;
    background-color: inherit;
    cursor: pointer;
    background-color:  #fff;
    width: 140px;
    height: 40px;
    border-radius: 30px;
    font-size: 1rem;
    font-weight: 600;
    margin: 30px;
    color:  #573ba3;
}

.save-comment-btn:hover{
    background-color: #573ba3;
    color: #fff;
    border-color: #573ba3;
}
@media all and (max-width:768px){
    .block-product-details{
        display: flex;
        flex-direction: column;
    }
    .details-description{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .user-name{
        display: flex;
        flex-direction: column;
        text-align: left;
        align-items: center;
    }
    .user-icon{
        width: 15% !important;
    }
    .comment{
        margin-left: 17% !important;
        font-size: 15px;
    }
}
@media all and (max-width:380px){
    .product-details-img{
        width: 80% !important;
    }
    .h3{
        font-size: 20px !important;
    }
    .block-product-details{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
.searchPage__info {
  padding: 20px;
}

.searchPage__info > p {
  margin-bottom: 10px;
  font-size: 14px;
}

.searchPage__info > h1 {
  margin-bottom: 30px;
}

.searchPage__info > button {
  border-radius: 30px;
  text-transform: inherit;
  margin: 5px;
}

.searchResult {
  display: flex;
  position: relative;
  margin: 20px;
  padding: 20px;
  border-bottom: 1px solid lightgray;
  cursor: pointer;
}

.searchResult :hover {
  opacity: 0.8;
}

.searchResult > img {
  height: 200px;
  width: 350px;
  border-radius: 10px;
  overflow: hidden;
}

.searchResult__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 20px;
}

.searchResult__heart {
  position: absolute;
  top: 20px;
  right: 40px;
}

.searchResult__infoTop {
  width: 40vw;
}

.searchResult__infoTop > h3 {
  font-weight: 300;
  margin-top: 10px;
  color: grey;
}

.searchResult__infoBottom {
  display: flex;
  justify-content: space-between;
}

.searchResult__star {
  color: red;
}

.searchResult__stars {
  display: flex;
  align-items: center;
}

.searchResult__price > p {
  text-align: right;
}

.searchResult__price {
  position: absolute;
  bottom: 20px;
  right: 30px;
}

